<template>
  <div id="WordsAndColors" class="pb-16">
    
    <Alert ref="Alert"/>

    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :maxW="'max-w-2xl'" :score="score" ref="InteractiveBord"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3">

        <div class="w-full flex justify-center">
          <div class="p-1 w-1/2 h-32">
            <div class="h-full w-full flex items-center justify-center rounded-lg overflow-hidden border text-4xl bg-gray-800" :style="'color: '+colorText" v-text="colorName"/>
          </div>
        </div>
        <div class="w-full flex flex-wrap mt-8">
          <div class="p-3" :class="{'w-1/2 h-32':level==0,'w-1/3 h-32':level==1,'w-1/3 h-24':level==2}" v-for="(color, index) in colors" :key="index">
            <div class="h-full w-full rounded-lg overflow-hidden cursor-pointer border" :style="'background-color:'+color.color" @click="clickColor(color.name)" />
          </div>
        </div>

      </div>
    </div>
    
    <LevelsSelect @dataForApp="updateLevelValue" />

  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'WordsAndColors',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      colorName:null,
      colorText:null,

      colors:[],

      score:0,
      stopGame:false,

      level:0,

      allColors:[
        {0:"blue",1:"#0000ff"},
        {0:"green",1:"#008000"},
        {0:"red",1:"#ff0000"},
        {0:"gray",1:"#808080"},
        {0:"yellow",1:"#ffff00"},
        {0:"pink",1:"#ffc0cb"},
        {0:"orange",1:"#ffa500"},
        {0:"purple",1:"#800080"},
        {0:"brown",1:"#a52a2a"}
      ]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.addColors();
    },
    clickColor(clickcolor){
      var vm=this;
      var alert = vm.$refs.Alert;
      if (vm.stopGame) return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();

      if (clickcolor == vm.colorName) {
        vm.score += 2;
        alert.alertShow("+2")
        console.log("true")
      }else{
        if(vm.score > 0)vm.score -= 1;
        if(vm.score > 0)alert.alertShow("-1","bg-red-500")
        else alert.alertShow("0","bg-red-500")
        console.log("false")
      }
      vm.addColors();
    },
    addColors(){
      this.colors = [];
      var a = this.allColors;
      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }


      for (let n = 0; n < this.getLevelRang(); n++) {
        this.colors.push({
          name:this.allColors[n][0],
          color:this.allColors[n][1],
        });
      }

      this.colorName = this.colors[Math.floor(Math.random() * this.colors.length)].name;
      this.colorText = this.colors[Math.floor(Math.random() * this.colors.length)].color;
    },
    getLevelRang(){
      var num = 4;
      if(this.level == 0)num=4;
      else if(this.level == 1)num=6;
      else if(this.level == 2)num=9;

      return num;
    },

    updateTimeOver(){
      this.stopGame = true;
      this.$refs.Alert.alertTimeOver();
    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  }
}
</script>

